@value makerWhite,makerLime, makerDarkGreen, makerGrey,makerPink,makerLightGrey, makerDarkGreen, makerGreen from '../../Review/common/color.css';
@value fontEn from '../../Review/common/font.css';

.button_write {
    margin-left: 3rem;
    cursor: pointer;
    border: makerGrey 1px solid;
    background-color: makerDarkGreen;
    color: makerWhite;
    border-radius: 10px;
    height: 3rem;
    width: 6rem;
    font-weight: bold;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    border: none;
    transition: all 0.1s ease-in-out;
}

.button_write:hover {
    background-color: makerLime;
    color: makerDarkGreen;
}