.modalimg {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 99;
    background-color: rgba(0, 0, 0, 0.6);
}
.modalimg button {
    outline: none;
    cursor: pointer;
    border: 0;
}
.modalimg > section {
    width: 90%;
    max-width: 850px;
    max-height: 800px;
    margin: 0 auto;
    border-radius: 0.3rem;
    background-color: #fff;
    /* 팝업이 열릴때 스르륵 열리는 효과 */
    animation: modal-show 0.3s;
    overflow: hidden;
}
.modalimg > section > header {
    padding: 16px 64px 16px 16px;
    margin-left: 50px;
    font-weight: 700;
    color: #92af94;
}
.modalimg > section > header button {
    width: 30px;
    font-size: 21px;
    font-weight: 700;
    color: #999;
    background-color: transparent;
}
.modalimg > section > main {
    padding: 16px;
    border-bottom: 1px solid #dee2e6;
    border-top: 1px solid #dee2e6;
}
.modalimg > section > footer {
    padding: 12px 16px;
    text-align: right;
}
.modalimg > section > footer button {
    padding: 6px 12px;
    color: #fff;
    background-color: #92af94;
    border-radius: 5px;
    font-size: 13px;
}
.modalimg.openModal {
    display: flex;
    align-items: center;
    /* 팝업이 열릴때 스르륵 열리는 효과 */
    animation: modal-bg-show 0.3s;
}
@keyframes modal-show {
    from {
        opacity: 0;
        margin-top: -50px;
    }
    to {
        opacity: 1;
        margin-top: 0;
    }
}
@keyframes modal-bg-show {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}